import axios from "axios";
import emitter from "@/eventBus";
import { useChatStore } from "@/stores/chatStore";

const instance = axios.create({
  baseURL: "/", // 로컬 프론트에서 돌릴때
  //baseURL: "http://localhost:9090", // 로컬 백엔드에서 돌릴때
  //baseURL: "https://oral-therine-heesu-f4935ad1.koyeb.app", // 백엔드 서버 주소
});

instance.interceptors.request.use(
  (config) => {
    try {
      const token = localStorage.getItem("jwt");
      if (token) {
        config.headers.Authorization = `Bearer ${token}`;
      }
    } catch (error) {
      console.log("axios response interceptor catch: ", error);
    }
    return config;
  },
  (error) => {
    console.log("axios response interceptor error: ", error);
    return Promise.reject(error);
  }
);

instance.interceptors.response.use(
  (response) => response,
  async (error) => {
    const originalRequest = error.config;
    const chatStore = useChatStore();

    // 401 && 아직 retry 안한 요청만
    if (
      error.response &&
      error.response.status === 401 &&
      !originalRequest._retry
    ) {
      originalRequest._retry = true;

      try {
        // jwt의 payload 에서 userid 추출
        const token = localStorage.getItem("jwt");
        const payload = JSON.parse(atob(token.split(".")[1]));

        // refresh 호출
        const refreshResponse = await axios.post("/api/refreshJwt", {
          withCredentials: true, // 쿠키 자동 전송 옵션
        });

        // 로컬 저장소 업데이트
        const newAccessToken = refreshResponse.data.jwt;
        if (newAccessToken == null) {
          alert("로그인 유효시간이 만료되었습니다.");
          //router.push("/"); 프론트만됨
          window.location.href = window.location.origin;
          localStorage.clear();
          chatStore.resetStore();
        } else {
          localStorage.setItem("jwt", newAccessToken);

          // Authorization 헤더 갱신 후 원래 요청 재시도
          originalRequest.headers["Authorization"] = `Bearer ${newAccessToken}`;

          // App.vue로
          emitter.emit("trigger-loginCheck");

          return instance(originalRequest);
        }
      } catch (refreshError) {
        // Refresh Token 실패 시 로그인 페이지 이동 등 처리
        console.error("Refresh failed", refreshError);
        // 예: window.location.href = '/login';
        return Promise.reject(refreshError);
      }
    }

    return Promise.reject(error);
  }
);

export default instance;
